/**
 * Djangoã®CSRFãƒˆãƒ¼ã‚¯ãƒ³ã‚’ã‚¯ãƒƒã‚­ãƒ¼ã‹ã‚‰å–å¾—ã™ã‚‹ãŸã‚ã®ãƒ˜ãƒ«ãƒ‘ãƒ¼é–¢æ•°
 * @param name ã‚¯ãƒƒã‚­ãƒ¼åï¼ˆé€šå¸¸ã¯ 'csrftoken'ï¼‰
 * @returns ãƒˆãƒ¼ã‚¯ãƒ³ã®æ–‡å­—åˆ—ã€ã¾ãŸã¯null
 */
function getCookie(name: string): string | null {
    let cookieValue = null;
    if (document.cookie && document.cookie !== '') {
        const cookies = document.cookie.split(';');
        for (let i = 0; i < cookies.length; i++) {
            const cookie = cookies[i].trim();
            // æ¢ã—ã¦ã„ã‚‹åå‰ã§å§‹ã¾ã‚‹ã‚¯ãƒƒã‚­ãƒ¼ã‹ï¼Ÿ (ä¾‹: "csrftoken=...")
            if (cookie.substring(0, name.length + 1) === (name + '=')) {
                cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                break;
            }
        }
    }
    return cookieValue;
}

export async function chatApi(request: ChatAppRequest, token: string | null): Promise<Response> {

    const csrfToken = getCookie('csrftoken');
    const headers = {
        'Content-Type': 'application/json',
        'X-CSRFToken': csrfToken || '' ,
        ...(token ? { Authorization: `Bearer ${token}` } : {}),
    };

    return await fetch('/api/chat/', {
        method: "POST",
        credentials: "include",
        headers: headers,
        body: JSON.stringify(request)
    });
}

export function getCitationFilePath(citation: string): string {
    return `${BACKEND_URI}/content/${citation}`;
}

