/**
 * ä¼šè©±å±¥æ­´ã‚’ãƒãƒƒã‚¯ã‚¨ãƒ³ãƒ‰APIçµŒç”±ã§Cosmos DBã«ä¿å­˜ã™ã‚‹é–¢æ•°
 * @param data ä¿å­˜ã™ã‚‹ãƒ‡ãƒ¼ã‚¿
 * @param token èªè¨¼ãƒˆãƒ¼ã‚¯ãƒ³
 */
export async function saveConversationToDb(data: ConversationData, token: string | undefined): Promise<void> {
    // ãƒãƒƒã‚¯ã‚¨ãƒ³ãƒ‰ã«ä½œæˆã—ãŸAPIã‚¨ãƒ³ãƒ‰ãƒã‚¤ãƒ³ãƒˆã®URL
    const apiEndpoint = `/api/history/`;

    // èªè¨¼ãƒˆãƒ¼ã‚¯ãƒ³ãŒãªã„å ´åˆã¯å‡¦ç†ã‚’ä¸­æ–­
    if (!token) {
        console.warn("No token available. Skipping save to DB.");
        return;
    }

    try {
        const response = await fetch(apiEndpoint, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                "Authorization": `Bearer ${token}`
            },
            body: JSON.stringify(data)
        });

        if (!response.ok) {
            // APIå‘¼ã³å‡ºã—ãŒå¤±æ•—ã—ãŸå ´åˆã®ã‚¨ãƒ©ãƒ¼ãƒãƒ³ãƒ‰ãƒªãƒ³ã‚°
            console.log(response);
            const errorData = await response.json();
            throw new Error(errorData.message || "Failed to save conversation to DB");
        }

        console.log("Conversation saved successfully.");

    } catch (error) {
        console.error("Error saving conversation:", error);
        // ã“ã“ã§UIã«ã‚¨ãƒ©ãƒ¼é€šçŸ¥ã‚’å‡ºã™ãªã©ã®å‡¦ç†ã‚‚å¯èƒ½
    }
}



/**
 * èªè¨¼ãƒ»CSRFå¯¾ç­–æ¸ˆã¿ã®POSTãƒªã‚¯ã‚¨ã‚¹ãƒˆã‚’ãƒãƒƒã‚¯ã‚¨ãƒ³ãƒ‰ã«é€ä¿¡ã—ã¾ã™ã€‚
 * @param url APIã®ã‚¨ãƒ³ãƒ‰ãƒã‚¤ãƒ³ãƒˆ (ä¾‹: '/api/chat')
 * @param request é€ä¿¡ã™ã‚‹ãƒ‡ãƒ¼ã‚¿ã‚ªãƒ–ã‚¸ã‚§ã‚¯ãƒˆ
 * @param authToken ãƒãƒƒã‚¯ã‚¨ãƒ³ãƒ‰ã‚»ãƒƒã‚·ãƒ§ãƒ³ç”¨ã®èªè¨¼ãƒˆãƒ¼ã‚¯ãƒ³
 * @returns fetchã®ãƒ¬ã‚¹ãƒãƒ³ã‚¹
 */
export async function postApi(url: string, request: any, authToken: string | null) {
    // DjangoãŒã‚»ãƒƒãƒˆã—ãŸCSRFãƒˆãƒ¼ã‚¯ãƒ³ã‚’ã‚¯ãƒƒã‚­ãƒ¼ã‹ã‚‰å–å¾—
    const csrfToken = getCookie('csrftoken');

    if (!authToken) {
        // èªè¨¼ãƒˆãƒ¼ã‚¯ãƒ³ãŒãªã„å ´åˆã¯ã‚¨ãƒ©ãƒ¼ï¼ˆãƒ­ã‚°ã‚¤ãƒ³ã—ã¦ã„ãªã„å¯èƒ½æ€§ï¼‰
        throw new Error("èªè¨¼ãƒˆãƒ¼ã‚¯ãƒ³ãŒã‚ã‚Šã¾ã›ã‚“ã€‚ãƒ­ã‚°ã‚¤ãƒ³ã—ã¦ã„ãªã„å¯èƒ½æ€§ãŒã‚ã‚Šã¾ã™ã€‚");
    }
    
    if (!csrfToken) {
        // CSRFãƒˆãƒ¼ã‚¯ãƒ³ãŒè¦‹ã¤ã‹ã‚‰ãªã„å ´åˆã€è­¦å‘Šã‚’å‡ºã™
        console.warn("CSRFãƒˆãƒ¼ã‚¯ãƒ³ãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“ã€‚Djangoã¸ã®POSTãƒªã‚¯ã‚¨ã‚¹ãƒˆã¯å¤±æ•—ã™ã‚‹å¯èƒ½æ€§ãŒã‚ã‚Šã¾ã™ã€‚");
    }

    return await fetch(url, {
        method: "POST",
        headers: {
            'Content-Type': 'application/json',
            // èªè¨¼ãƒˆãƒ¼ã‚¯ãƒ³ï¼ˆãƒ­ã‚°ã‚¤ãƒ³æ™‚ã«ãƒãƒƒã‚¯ã‚¨ãƒ³ãƒ‰ã‹ã‚‰å—ã‘å–ã£ãŸã‚‚ã®ï¼‰
            'Authorization': `Bearer ${authToken}`,
            // CSRFãƒˆãƒ¼ã‚¯ãƒ³ï¼ˆDjangoãŒã‚¯ãƒƒã‚­ãƒ¼ã«ã‚»ãƒƒãƒˆã—ãŸã‚‚ã®ï¼‰
            'X-CSRFToken': csrfToken || '' // è¦‹ã¤ã‹ã‚‰ãªã„å ´åˆã¯ç©ºæ–‡å­—ã‚’é€ã‚‹
        },
        body: JSON.stringify(request)
    });
}