import React from 'react';
import ReactDOM from "react-dom/client";
import { createHashRouter, RouterProvider } from "react-router-dom";
import { I18nextProvider } from "react-i18next";
import { HelmetProvider } from "react-helmet-async";
import { initializeIcons } from "@fluentui/react";
import { EventType, InteractionType, EventMessage, AuthenticationResult } from "@azure/msal-browser";
import { MsalProvider, MsalAuthenticationTemplate } from "@azure/msal-react";

import "./index.css";
import Chat from "./pages/chat/Chat";
import LayoutWrapper from "./layoutWrapper";
import i18next from "./i18n/config";
import { AuthHandler } from "./AuthHandler";
import { msalInstance, loginRequest } from './authConfig';
import { AuthProvider } from './AuthContext';
import ChatWrapper from "./ChatWrapper";

// ãƒ«ãƒ¼ã‚¿ãƒ¼ã®å®šç¾©
const router = createHashRouter([
  {
    path: "/",
    element: (
      <MsalAuthenticationTemplate
        interactionType={InteractionType.Redirect}
        authenticationRequest={loginRequest}
      >
        <LayoutWrapper />
      </MsalAuthenticationTemplate>
    ),
    children: [
      {
        index: true,
        element: (
          <AuthHandler>
            <Chat />
          </AuthHandler>
        ),
      },
      {
        path: "chat/:id",
        element: (
          <AuthHandler>
            <ChatWrapper />
          </AuthHandler>
        ),
      },
    ],
  },

  // âœ… ChatWrapper ã‚’å€‹åˆ¥ã®ãƒ«ãƒ¼ãƒˆã§è¿½åŠ 
//   {
//     path: "chat/:id",
//     element: (
//       <AuthHandler>
//         <ChatWrapper />
//       </AuthHandler>
//     ),
//   },
]);

// â˜…â˜…â˜… ä¿®æ­£ç‚¹ â˜…â˜…â˜…
// ã‚¢ãƒ—ãƒªã‚±ãƒ¼ã‚·ãƒ§ãƒ³ã‚’èµ·å‹•ã™ã‚‹ãŸã‚ã®éåŒæœŸé–¢æ•°ã‚’å®šç¾©ã—ã¾ã™
async function main() {
    // 1. MSALã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹ã®åˆæœŸåŒ–ã‚’å¾…ã¡ã¾ã™ (ã“ã‚ŒãŒæŠœã‘ã¦ã„ã¾ã—ãŸ)
    await msalInstance.initialize();

    // ã‚¤ãƒ™ãƒ³ãƒˆã‚³ãƒ¼ãƒ«ãƒãƒƒã‚¯ã®è¨­å®š
    msalInstance.addEventCallback((event: EventMessage) => {
        if (event.eventType === EventType.LOGIN_SUCCESS && event.payload) {
            const payload = event.payload as AuthenticationResult;
            if (payload.account) {
                msalInstance.setActiveAccount(payload.account);
            }
        }
    });

    // 2. ãƒªãƒ€ã‚¤ãƒ¬ã‚¯ãƒˆå‡¦ç†ã‚’å¾…ã¡ã¾ã™
    await msalInstance.handleRedirectPromise();

    // 3. å…¨ã¦ã®MSALå‡¦ç†ãŒçµ‚ã‚ã£ãŸå¾Œã«ã€Reactã‚¢ãƒ—ãƒªã‚’ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°ã—ã¾ã™
    initializeIcons();
    ReactDOM.createRoot(document.getElementById("root") as HTMLElement).render(
        <React.StrictMode>
            <MsalProvider instance={msalInstance}>
                <AuthProvider>
                    <I18nextProvider i18n={i18next}>
                        <HelmetProvider>
                            <RouterProvider router={router} />
                        </HelmetProvider>
                    </I18nextProvider>
                </AuthProvider>
            </MsalProvider>
        </React.StrictMode>
    );
}

// éåŒæœŸé–¢æ•°ã‚’å®Ÿè¡Œã—ã¦ã‚¢ãƒ—ãƒªã‚’èµ·å‹•
main().catch(error => {
    console.error("ã‚¢ãƒ—ãƒªã‚±ãƒ¼ã‚·ãƒ§ãƒ³ã®åˆæœŸåŒ–ä¸­ã«ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸ:", error);
});